<template>
  <div class="container">
    <vue-headful
      title="Writing | Rhys Matthew | Web Developer"
      description="Rhys Matthew is based in Nottingham, UK. He has a passion for building digital interfaces and creating interactive websites. Rhys loves clean modular code and modern JavaScript frameworks like Vue or React."
      keywords="Writing by Rhys Matthew, writing, UI developer, UX Design, Web Design, Front-End Development, JavaScript dev, Freelance, web engineer, CSS, PHP, Wordpress developer"
      :head="{
        'meta[property=\'og:url\']': {content: 'https://rhys.digital/writing/'},
        'meta[name=\'twitter:site\']': {content: 'https://rhys.digital/writing/'},
        'link[rel=\'canonical\']': {href: 'https://rhys.digital/writing/'}
      }"
    />

    <section class="section pb-0">
      <div class="mw--650">
        <div class="section__header">
          <h1 class="h2 reveal" tabindex="0"><div class="reveal-inner">Writing</div></h1>
        </div>
        <div class="section__block">

          <div v-if="posts.length !== 0" class="posts">
            <div v-for="(post, index) in posts" :key="post.id" class="post">
              <router-link :to="linkResolver(post)">
                <p>{{ post.data }}</p>
                <h2>{{ post.data.title }}</h2>
                <p class="post__date">{{ post.data.posted_date }}</p>
                <div>
                  <p>{{ post.data.excerpt[index].text }}</p>
                </div>
              </router-link>
            </div>
          </div>

          <div class="reveal reveal--delay-1">
            <div class="reveal-inner">

            </div>
          </div>
        </div>
      </div>
    </section>
  </div>
</template>

<script>
// Reveal
import reveal from '../components/reveal/_reveal.js'

export default {
  data: function () {
    return {
      posts: [],
      dateOptions: { year: 'numeric', month: 'short', day: '2-digit' },
      linkResolver: this.$prismic.linkResolver
    }
  },
  created () {
    this.getPosts()
  },
  mounted () {
    // eslint-disable-next-line
    setTimeout (() => {
      // Dispatch pageChange event
      //
      const pageChange = new CustomEvent('pageChange')
      document.dispatchEvent(pageChange)

      // Initialise reveal effect after content has had
      // enough time to render
      //
      reveal.init()
    }, 1500)
  },
  methods: {
    getPosts: function () {
      // Query to get blog posts
      this.$prismic.client.query(
        this.$prismic.Predicates.at('document.type', 'post'), { orderings: '[my.post.date desc]' }
      ).then((response) => {
        this.posts = response.results
      })
    }
  }
}
</script>
